<template>
  <div id="app">
    <transition name="fade" mode="out-in">
      <router-view/>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'app'
}
</script>

<style lang="less">
@import url("./assets/less/main");
@import url('./assets/less/reset');
.fade-enter-active,
.fade-leave-active{
  transition:all 0.2s ease;
  opacity: 1,
}
.fade-enter,
.fade-leave-active{
  opacity: 0
}
</style>



